import React from "react";
import { Col, Row } from "react-bootstrap";
import "./ElementoDesarrollo.scss";

const ElementoDesarrollo = ({
  title,
  category,
  direccion,
  img,
  orientation,
}) => {
  return (
    <>
      {orientation === "izq" ? (
        <Row>
          <Col xs={6}>
            <img src={img} alt="" className="img_elemento" />
          </Col>
          <Col xs={6} className="col_dat-elemento izq">
            <div className="div_dat-elemento izq">
              <h1 className="dat-elemento-h1">{title}</h1>
              <h6 className="dat-elemento-h6">{category}</h6>
              <h6 className="dat-elemento-h6">{direccion}</h6>
            </div>
          </Col>
        </Row>
      ) : (
        <Row>
          <Col xs={6} className="col_dat-elemento der">
            <div className="div_dat-elemento der">
              <h1 className="dat-elemento-h1">{title}</h1>
              <h6 className="dat-elemento-h6">{category}</h6>
              <h6 className="dat-elemento-h6">{direccion}</h6>
            </div>
          </Col>
          <Col xs={6}>
            <img src={img} alt="" className="img_elemento" />
          </Col>
        </Row>
      )}
    </>
  );
};

export default ElementoDesarrollo;
