import React from "react";
import { Col, Row } from "react-bootstrap";
import ElementoDesarrollo from "../../Home/DesarrollosSec/ElementoDesarrollo/ElementoDesarrollo";
import { desarrollos } from "../../utils/constants";

const DesUrbanos = () => {
  return (
    <Row>
      {desarrollos.map((el, i) => (
        <Col xs={6}>
          {(i / 2) % 2 === 0 || i === 1 ? (
            <ElementoDesarrollo
              title={el.title}
              category={el.category}
              direccion={el.direccion}
              img={el.img}
              orientation="izq"
            />
          ) : (
            <ElementoDesarrollo
              title={el.title}
              category={el.category}
              direccion={el.direccion}
              img={el.img}
              orientation="der"
            />
          )}
        </Col>
      ))}
    </Row>
  );
};

export default DesUrbanos;
